export default {
	data() {
		return {
			src: '',
			id: '',
			content:''
		}
	},
	onLoad(options) {
		this.id = options.id;
		this.loadData()
	},
	methods: {
		loadData() {
			this._reqw.ipost({
				id: this.id
			}, "helpDetail").then(res => {
				res.result == 0 ? this.content = res.content : this.$api.tip(res.resultNote)
			}).catch(err => {})
		}
	}
}
